import React, { useState, useEffect } from "react";
import { ChevronDown, X } from "lucide-react";
import avtar from "../../../public/assets/avtar.jpg";
import Image from "next/image";
import axios from "axios";

interface memberInterface {
  id: string;
  fullName: string;
  role: string;
  position: string;
  avatar: string;
}

interface AddTeamMembersProps {
  onClose: () => void;
  onAddMember: (newMember: memberInterface) => void;
  teamMembers: memberInterface[];
}

const AddTeamMembers: React.FC<AddTeamMembersProps> = ({
  onClose,
  onAddMember,
  teamMembers,
}) => {
  const [selectedMembers, setSelectedMembers] = useState<memberInterface[]>([]);
  const [search, setSearch] = useState("");
  const [membersToBeAdded, setMembersToBeAdded] = useState<memberInterface[]>([]);
  const [filteredMembers, setFilteredMembers] = useState<memberInterface[]>([]);
  const [showUserList, setShowUserList] = useState<boolean>(false);
  const [members, setMembers] = useState([
    {
      id: "1",
      fullName: "Esther Howard",
      role: "",
      position: "Project Manager",
      avatar: "/api/placeholder/40/40",
    },
    // ... other initial members
  ]);

  const handleSelectMember = (member: memberInterface) => {
    if (!selectedMembers.some(m => m.id === member.id)) {
      setSelectedMembers(prev => [...prev, member]);
    }
    setShowUserList(false);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setShowUserList(true);
    setSearch(e.target.value);
  };

  const handleRemoveSelectedMember = (memberId: string) => {
    setSelectedMembers(prev => prev.filter(m => m.id !== memberId));
  };

  const handleAddMember = () => {
    // Add selected members to membersToBeAdded
    const newMembersToAdd = selectedMembers.filter(
      member => !membersToBeAdded.some(m => m.id === member.id)
    );
    
    setMembersToBeAdded(prev => [...prev, ...newMembersToAdd]);
    setSelectedMembers([]); // Clear selected members
    setShowUserList(false);
    setSearch("");
  };

  const handleRoleSelect = (memberId: string, role: string) => {
    setMembersToBeAdded(prev => {
      const updatedMembers = prev.map(member =>
        member.id === memberId ? { ...member, role } : member
      );
      
      // Notify parent component about the role change
      const updatedMember = updatedMembers.find(m => m.id === memberId);
      if (updatedMember) {
        onAddMember(updatedMember);
      }
      
      return updatedMembers;
    });
  };

  // Filter members for the dropdown
  useEffect(() => {
    const filtered = members.filter(member => {
      const isNotSelected = !selectedMembers.some(m => m.id === member.id);
      const isNotAdded = !membersToBeAdded.some(m => m.id === member.id);
      const isNotInTeam = !teamMembers.some(m => m.id === member.id);
      const matchesSearch = member.fullName.toLowerCase().includes(search.toLowerCase().trim());
      
      return isNotSelected && isNotAdded && isNotInTeam && matchesSearch;
    });
    
    setFilteredMembers(filtered);
  }, [members, selectedMembers, membersToBeAdded, teamMembers, search]);

  // Rest of your component remains the same...
  return (
    <div className="fixed inset-0 flex items-center justify-center h-auto bg-black/50">
      <div className="bg-white rounded-xl h-[500px] shadow-lg w-[480px] p-5 relative">
        {/* Header */}
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-semibold text-gray-900">
            Add Team Members
          </h2>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600"
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        {/* Search and Add Section */}
        <div className="flex relative w-full">
          <div className="flex items-center w-full gap-2">
            <div className="flex flex-grow p-0.5 overflow-y-auto border-2 rounded-lg min-h-[40px] max-h-auto">
              {/* Selected members chips */}
              {selectedMembers.map((member) => (
                <div
                  key={member.id}
                  className="flex items-center m-0.5 gap-2 bg-[#5D56BD] rounded-full px-2"
                >
                  <img
                    src={member.avatar}
                    alt=""
                    className="w-5 h-5 rounded-full"
                  />
                  <div className="text-xs">
                    <p className="font-medium text-[#FFFFFF] leading-none">
                      {member.fullName}
                    </p>
                  </div>
                  <button
                    onClick={() => handleRemoveSelectedMember(member.id)}
                    className="text-[#FFFFFF] hover:text-red-500"
                  >
                    <X className="w-4 h-4" />
                  </button>
                </div>
              ))}

              <input
                type="text"
                placeholder="Add others by name"
                className="flex-grow px-3 py-2 min-w-[120px] text-sm border-none outline-none"
                onChange={handleInputChange}
                value={search}
              />
            </div>
            <button
              onClick={handleAddMember}
              className="px-6 py-2 bg-[#5D56BD] text-white rounded-lg hover:bg-indigo-700 text-sm font-medium"
            >
              Add
            </button>
          </div>
        </div>

        {/* Dropdown List */}
        <div className="relative">
          {showUserList && (
            <div className="mt-1 absolute border-2 rounded-lg bg-white text-sm w-[358px] z-50 shadow-lg">
              {filteredMembers.slice(0, 5).map((member, index) => (
                <ul key={index} className="flex text-[#333333] px-1">
                  <li>
                    <Image
                      src={avtar}
                      alt={member.fullName}
                      className="w-10 h-10 rounded-full m-2"
                    />
                  </li>
                  <li
                    onClick={() => handleSelectMember(member)}
                    className="cursor-pointer px-2 py-2 hover:bg-[#F7F7FC] w-full mt-2"
                  >
                    {member.fullName}
                  </li>
                </ul>
              ))}
            </div>
          )}
        </div>

        {/* Added Members List */}
        <div className="absolute mt-4 w-[430px] overflow-x-hidden z-0">
          <div className="flex text-sm justify-between text-gray-500 mb-4 z-0">
            <span>Team members ({teamMembers.length})</span>
            <span className="mr-20">Roles</span>
          </div>

          <div className="space-y-4">
            {membersToBeAdded.map((member) => (
              <div
                key={member.id}
                className="flex items-center justify-between cursor-pointer"
              >
                <div className="flex items-center gap-3">
                  <img
                    src={member.avatar}
                    alt={member.fullName}
                    className="w-10 h-10 rounded-full p-0.5"
                  />
                  <div>
                    <p className="text-sm font-medium text-gray-900">
                      {member.fullName}
                    </p>
                    <p className="text-sm text-gray-500">{member.position}</p>
                  </div>
                </div>
                <select
                  className="px-3 py-1 text-sm border border-gray-200 rounded-lg"
                  value={member.role || ""}
                  onChange={(e) => handleRoleSelect(member.id, e.target.value)}
                >
                  <option value="">Select Role</option>
                  <option value="owner">Owner</option>
                  <option value="admin">Admin</option>
                  <option value="user">User</option>
                  <option value="client">Client</option>
                </select>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddTeamMembers;